ión, que trata de una entrevista o reunión grupal. Teniendo en cuenta esto, en las reuniones de trabajo que normalmente se desarrollan dentro de las organizaciones, se pueden identificar necesidades, y para hacerlo, el área de Gestión del Talento Humano debe estar presente, según se considere necesario.")])])])
